import React from 'react';
import { View, StyleSheet, ViewStyle } from 'react-native';
import { colors, borderRadius } from '../../constants/theme';
import Text from './Text';

export interface AgentAvatarProps {
  // Êô∫ËÉΩ‰ΩìÁ±ªÂûã
  agent: 'xiaoai' | 'xiaoke' | 'laoke' | 'soer';
  
  // Â∞∫ÂØ∏
  size?: 'small' | 'medium' | 'large' | 'xlarge' | number;
  
  // Áä∂ÊÄÅ
  online?: boolean;
  
  // Ëá™ÂÆö‰πâÊ†∑Âºè
  style?: ViewStyle;
  
  // ÂÖ∂‰ªñÂ±ûÊÄß
  testID?: string;
}

const AgentAvatar: React.FC<AgentAvatarProps> = ({
  agent,
  size = 'medium',
  online,
  style,
  testID,
}) => {
  const getSize = () => {
    if (typeof size === 'number') {
      return size;
    }
    
    switch (size) {
      case 'small':
        return 32;
      case 'medium':
        return 48;
      case 'large':
        return 64;
      case 'xlarge':
        return 96;
      default:
        return 48;
    }
  };

  const avatarSize = getSize();

  const getAgentConfig = () => {
    switch (agent) {
      case 'xiaoai':
        return {
          name: 'Â∞èËâæ',
          emoji: 'ü§ñ',
          backgroundColor: colors.primary,
          description: 'AIÂä©Êâã',
        };
      case 'xiaoke':
        return {
          name: 'Â∞èÂÖã',
          emoji: 'üë®‚Äç‚öïÔ∏è',
          backgroundColor: colors.secondary,
          description: 'ÂÅ•Â∫∑È°æÈóÆ',
        };
      case 'laoke':
        return {
          name: 'ËÄÅÂÖã',
          emoji: 'üë¥',
          backgroundColor: colors.tcm.jade,
          description: '‰∏≠Âåª‰∏ìÂÆ∂',
        };
      case 'soer':
        return {
          name: 'Á¥¢ÂÑø',
          emoji: 'üß¨',
          backgroundColor: colors.tcm.gold,
          description: 'Êï∞ÊçÆÂàÜÊûêÂ∏à',
        };
      default:
        return {
          name: 'Êú™Áü•',
          emoji: '‚ùì',
          backgroundColor: colors.gray500,
          description: 'Êú™Áü•Êô∫ËÉΩ‰Ωì',
        };
    }
  };

  const agentConfig = getAgentConfig();

  const avatarStyle = [
    styles.base,
    {
      width: avatarSize,
      height: avatarSize,
      borderRadius: avatarSize / 2,
      backgroundColor: agentConfig.backgroundColor,
    },
    style,
  ].filter(Boolean) as ViewStyle[];

  return (
    <View style={styles.container} testID={testID}>
      <View style={avatarStyle}>
        <Text
          style={{
            fontSize: avatarSize * 0.4,
            textAlign: 'center',
          }}
        >
          {agentConfig.emoji}
        </Text>
      </View>
      
      {online !== undefined && (
        <View
          style={[
            styles.statusIndicator,
            {
              width: avatarSize * 0.25,
              height: avatarSize * 0.25,
              borderRadius: (avatarSize * 0.25) / 2,
              backgroundColor: online ? colors.success : colors.gray400,
              right: avatarSize * 0.05,
              bottom: avatarSize * 0.05,
            },
          ]}
        />
      )}
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    position: 'relative',
  },
  
  base: {
    alignItems: 'center',
    justifyContent: 'center',
    overflow: 'hidden',
  },
  
  statusIndicator: {
    position: 'absolute',
    borderWidth: 2,
    borderColor: colors.white,
  },
});

export default AgentAvatar;